import React, {Component} from 'react';
import {TweenMax} from 'gsap'
import './About.css'
import Arrow from "../Arrow/Arrow";

class About extends Component {
    constructor(props) {
        super(props)
        this.state = {}
        this.shrink = this.shrink.bind(this)
        this.grow = this.grow.bind(this)
        this.openLink = this.openLink.bind(this)
    }

    openLink(event) {
        window.open(event.target.dataset.link, '_blank');
    }

    grow(event) {
        TweenMax.to(event.target, .75, {
            height: 70,
            ease: "Elastic.easeOut",
        })
        TweenMax.to(event.target, .3, {
            fill: event.target.dataset.color
        })
    }

    shrink(event) {
        TweenMax.to(event.target, .75, {
            height: 50,
            ease: "Elastic.easeOut",
        })
        TweenMax.to(event.target, .3, {
            fill: "black",
        })
    }

    render() {
        return (
            <div ref="About" className="About">
                <Arrow target="Home" direction="Up" scroll={this.props.scroll}/>
                <div className="Container">
                    <div className="Left">
                        <h2>About Me</h2>
                        <p> Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consequatur cumque ex
                            facilis
                            fugiat pariatur qui quo quod reiciendis saepe unde. Alias dolorem nisi ratione sed
                            ullam. Dolore eos officiis voluptas. Lorem ipsum dolor sit amet, consectetur adipisicing
                            elit. Alias blanditiis consequatur labore officia optio, veritatis voluptas voluptatem!
                            Animi eaque esse, eveniet illum nobis perspiciatis quae quam quo rem saepe? Lorem ipsum
                            dolor sit amet, consectetur adipisicing elit. At consequuntur eveniet ipsa maiores molestiae
                            odit placeat, repellendus voluptatum? Ab beatae blanditiis cumque debitis, earum laborum
                            nihil officiis quidem sunt vel. Lorem ipsum dolor sit amet, consectetur adipisicing elit.</p>
                    </div>
                    <div className="Right">
                        <h2>Find Me</h2>
                        <div className="Links">
                            <svg className="Icon" onMouseEnter={this.grow} onMouseLeave={this.shrink} data-color="black"
                                 onClick={this.openLink}
                                 aria-labelledby="simpleicons-github-icon" data-link="https://github.com/ajgcpg"
                                 role="img" viewBox="0 0 24 24"
                                 xmlns="http://www.w3.org/2000/svg"><title id="simpleicons-github-icon">GitHub</title>
                                <path
                                    d="M12 .297c-6.63 0-12 5.373-12 12 0 5.303 3.438 9.8 8.205 11.385.6.113.82-.258.82-.577 0-.285-.01-1.04-.015-2.04-3.338.724-4.042-1.61-4.042-1.61C4.422 18.07 3.633 17.7 3.633 17.7c-1.087-.744.084-.729.084-.729 1.205.084 1.838 1.236 1.838 1.236 1.07 1.835 2.809 1.305 3.495.998.108-.776.417-1.305.76-1.605-2.665-.3-5.466-1.332-5.466-5.93 0-1.31.465-2.38 1.235-3.22-.135-.303-.54-1.523.105-3.176 0 0 1.005-.322 3.3 1.23.96-.267 1.98-.399 3-.405 1.02.006 2.04.138 3 .405 2.28-1.552 3.285-1.23 3.285-1.23.645 1.653.24 2.873.12 3.176.765.84 1.23 1.91 1.23 3.22 0 4.61-2.805 5.625-5.475 5.92.42.36.81 1.096.81 2.22 0 1.606-.015 2.896-.015 3.286 0 .315.21.69.825.57C20.565 22.092 24 17.592 24 12.297c0-6.627-5.373-12-12-12"/>
                            </svg>
                            <svg className="Icon" onMouseEnter={this.grow} onMouseLeave={this.shrink}
                                 onClick={this.openLink}
                                 data-color="#007bb6" data-link="https://www.linkedin.com/in/aidan-gallagher-94ab84164/"
                                 aria-labelledby="simpleicons-linkedin-icon" role="img"
                                 viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><title
                                id="simpleicons-linkedin-icon">LinkedIn</title>
                                <path
                                    d="M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.477-.9 1.637-1.85 3.37-1.85 3.601 0 4.267 2.37 4.267 5.455v6.286zM5.337 7.433c-1.144 0-2.063-.926-2.063-2.065 0-1.138.92-2.063 2.063-2.063 1.14 0 2.064.925 2.064 2.063 0 1.139-.925 2.065-2.064 2.065zm1.782 13.019H3.555V9h3.564v11.452zM22.225 0H1.771C.792 0 0 .774 0 1.729v20.542C0 23.227.792 24 1.771 24h20.451C23.2 24 24 23.227 24 22.271V1.729C24 .774 23.2 0 22.222 0h.003z"/>
                            </svg>
                            <svg className="Icon" onMouseEnter={this.grow} onMouseLeave={this.shrink}
                                 data-color="#3B5998" aria-labelledby="simpleicons-facebook-icon" role="img"
                                 viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><title
                                id="simpleicons-facebook-icon">Facebook</title>
                                <path
                                    d="M22.676 0H1.324C.593 0 0 .593 0 1.324v21.352C0 23.408.593 24 1.324 24h11.494v-9.294H9.689v-3.621h3.129V8.41c0-3.099 1.894-4.785 4.659-4.785 1.325 0 2.464.097 2.796.141v3.24h-1.921c-1.5 0-1.792.721-1.792 1.771v2.311h3.584l-.465 3.63H16.56V24h6.115c.733 0 1.325-.592 1.325-1.324V1.324C24 .593 23.408 0 22.676 0"/>
                            </svg>
                            <svg className="Icon" onMouseEnter={this.grow} onMouseLeave={this.shrink}
                                 data-color="#1DA1F2" aria-labelledby="simpleicons-twitter-icon" role="img"
                                 viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><title
                                id="simpleicons-twitter-icon">Twitter</title>
                                <path
                                    d="M23.954 4.569c-.885.389-1.83.654-2.825.775 1.014-.611 1.794-1.574 2.163-2.723-.951.555-2.005.959-3.127 1.184-.896-.959-2.173-1.559-3.591-1.559-2.717 0-4.92 2.203-4.92 4.917 0 .39.045.765.127 1.124C7.691 8.094 4.066 6.13 1.64 3.161c-.427.722-.666 1.561-.666 2.475 0 1.71.87 3.213 2.188 4.096-.807-.026-1.566-.248-2.228-.616v.061c0 2.385 1.693 4.374 3.946 4.827-.413.111-.849.171-1.296.171-.314 0-.615-.03-.916-.086.631 1.953 2.445 3.377 4.604 3.417-1.68 1.319-3.809 2.105-6.102 2.105-.39 0-.779-.023-1.17-.067 2.189 1.394 4.768 2.209 7.557 2.209 9.054 0 13.999-7.496 13.999-13.986 0-.209 0-.42-.015-.63.961-.689 1.8-1.56 2.46-2.548l-.047-.02z"/>
                            </svg>
                        </div>
                    </div>
                </div>
                <Arrow target="Home" scroll={this.props.scroll}/>
            </div>
        )
    }
}

export default About


